.checkout-item {
   width: 100%;
   display: flex;
   min-height: 100px;
   border-bottom: 1px solid darkgrey;
   padding: 15px 0;
   font-size: 20px;
   align-items: center;

   .image-container {
      width: 23%;
      padding-right: 15px;

      img {
         width: 100%;
         height: 100%;
      }
   }

   .quantity,
   .price {
      width: 15%;
   }
   .name {
      width: 38%;
   }

   .quantity {
      padding-left: 20px;
   }

   .remove-button {
      padding-left: 12px;
      cursor: pointer;
   }
}
